.App-header {
  background-color: #1f2335;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.textbox {
  animation-duration: 2s;
  animation-name: show;
}

.shortabout {
  animation-duration: 1.5s;
  animation-name: show;
}

.hello {
  font-size: 1.2em;
  animation-duration: 1.5s;
  animation-name: show;
}

.profpic {
  width: 200px;
  height: auto;
  border-radius: 50%;
  animation-duration: 2s;
  animation-name: show;
  outline: 4px solid rgb(204,255,255);
  border: 6px solid rgba(255, 255, 255, 0);
}

h3 {
  text-decoration: underline;
  text-decoration-color: #ccffff;
  text-underline-offset: 6px;
  text-decoration-thickness: 3px;
  animation-duration: 0.5s;
  animation-name: right;
}
*{
  font-family: 'Ubuntu';
}

a {
  color: #ccffff;
  text-decoration: none;
  line-height: 1.5;
}

.neonline {
  color: rgb(204,255,255);
  background-color: rgb(204,255,255);
  border-color : rgb(204,255,255);
  height: 5;
  box-shadow: 0px 0px 16px rgb(204,255,255);
  animation-duration: 1s;
  animation-name: width;
}

@keyframes width {
  0% { width: 0%; }
  100% { width: 100%; }
}

@keyframes neon {
  0% { opacity: 80%; }
  50% { opacity: 100%; }
  100% { opacity: 80%; }
}

@keyframes left {
  from {
    margin-left: 100%;
    opacity: 0%;
  }

  to {
    margin-left: 0%;
    opacity: 100%;
  }
}
@keyframes right {
  from {
    margin-left: -10%;
    opacity: 0%;
  }

  to {
    margin-left: 0%;
    opacity: 100%;
  }
}
@keyframes up {
  from {
    margin-bottom: -10%;
    opacity: 0%;
  }

  to {
    margin-bottom: 0%;
    opacity: 100%;
  }
}
@keyframes show {
  from {
    opacity: 0%;
  }

  to {
    opacity: 100%;
  }
}
